<template>
  <div class="hstory">
    <div class="search-record-hot">
      <div class="record">
        <div class="search-del">
          <span>最近搜索</span>
          <span @click="del">清除</span>
        </div>
        <div class="record-list">
          <van-button
            size="small"
            v-for="(item,index) in list"
            :key="index"
            @click="add(item)"
          >{{item}}</van-button>
        </div>

        <div class="search-del">
          <span>热门搜索</span>
          <span @click="random">换一批</span>
        </div>
        <div class="record-list">
          <van-button
            size="small"
            v-for="(item,index) in Hotlist"
            :key="index"
            @click="add(item)"
          >{{item}}</van-button>
        </div>
      </div>
    </div>
    <hx-Recommended></hx-Recommended>
  </div>
</template>

<script>
import hxRecommended from "../../components/hxRecommended";

export default {
  data() {
    return {
      list: [],
      Hotlist: [
        "山地车",
        "阿迪达斯球鞋",
        "苹果8",
        "iphone8P",
        "5G",
        "戴尔",
        "联想",
        "华为"
      ]
    };
  },
  methods: {
    add(item) {
      this.$router.push({ name: "searchkeywords", query: item });
    },
    del() {
      this.$store.commit("search/del");
      this.list = [];
    },
    // 随机热门
    random() {
      let arry = [];
      for (let i = 1; i <= 4; i++) {
        let index = Math.floor(
          Math.random() * (this.Hotlist.length - 1 + 1) + 0
        );
        arry.push(this.Hotlist[index]);
        this.Hotlist.splice(index, 1);
      }
      this.Hotlist = arry;
    }
  },
  created() {
    document.body.scrollTop = 0;
    document.documentElement.scrollTop = 0;
    this.list = this.$store.state.search.list;
    this.random();
  },
  components: {
    hxRecommended
  }
};
</script>

<style lang="less" scoped>
.hstory {
  .search-record-hot {
    margin-bottom: 20px;
    background-color: #ffffff;
    > .record {
      font-size: 13px;
      > .search-del {
        height: 20px;
        padding: 10px 0 10px 0;
        span:nth-child(1) {
          float: left;
          margin-left: 20px;
        }
        span:nth-child(2) {
          float: right;
          margin-right: 20px;
          color: #3985ff;
        }
      }
      > .record-list {
        padding: 20px 20px 20px 20px;
        button {
          margin: 5px;
          background-color: #efefef;
        }
      }
    }
  }
}
</style>